<template>
  <view class="login-container">
    <view class="login-header">
      <text class="title">Áî®Êà∑ÁôªÂΩï</text>
    </view>
    
    <view class="login-form">
      <view class="form-item">
        <text class="label">Áî®Êà∑Âêç</text>
        <input 
          class="input" 
          v-model="form.username" 
          placeholder="ËØ∑ËæìÂÖ•Áî®Êà∑Âêç"
          maxlength="20"
        />
      </view>
      
      <view class="form-item input-with-suffix">
        <text class="label">ÂØÜÁ†Å</text>
        <view class="input-wrapper">
          <input 
            class="input" 
            v-model="form.password" 
            :password="!loginPwdVisible"
            placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
            maxlength="20"
          />
          <view class="suffix-group">
            <view class="clear-toggle" v-if="form.password" @click.stop="form.password = ''">
              <text class="icon-clear">‚úï</text>
            </view>
            <view class="eye-toggle" @click.stop="loginPwdVisible = !loginPwdVisible">
              <text class="icon-eye">{{ loginPwdVisible ? 'üëÅÔ∏è' : 'üôà' }}</text>
            </view>
          </view>
        </view>
      </view>
      
      <button class="login-btn" @click="handleLogin" :disabled="loading">
        {{ loading ? 'ÁôªÂΩï‰∏≠...' : 'ÁôªÂΩï' }}
      </button>
      
      <view class="register-tip">
        ËøòÊ≤°ÊúâË¥¶Âè∑Ôºü<text class="register-link" @click="navigateToRegister">Á´ãÂç≥Ê≥®ÂÜå</text>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      form: {
        username: '',
        password: ''
      },
      loading: false,
      loginPwdVisible: false
    }
  },
  methods: {
    async handleLogin() {
      // Ë°®ÂçïÈ™åËØÅ
      if (!this.form.username.trim()) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•Áî®Êà∑Âêç',
          icon: 'none'
        });
        return;
      }
      
      if (!this.form.password) {
        uni.showToast({
          title: 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å',
          icon: 'none'
        });
        return;
      }
      
      try {
        this.loading = true;
        
        // Ë∞ÉÁî®‰∫ëÂáΩÊï∞ËøõË°åÁôªÂΩï
        const res = await uniCloud.callFunction({
          name: 'userLogin',
          data: {
            username: this.form.username.trim(),
            password: this.form.password
          },
          timeout: 10000
        });
        
        if (res.result.code === 0) {
          // ÁôªÂΩïÊàêÂäü
          uni.showToast({
            title: 'ÁôªÂΩïÊàêÂäü',
            icon: 'success'
          });
          
          // ‰ªÖ‰ΩøÁî®Êú¨Âú∞ userInfo ‰Ωú‰∏∫ÁôªÂΩïÊÄÅÔºåÂπ∂ÊòæÂºèÂ≠òÂÇ® usernameÔºàÊ≥®ÊÑèÂêéÁ´ØËøîÂõûÁªìÊûÑÂú® data ÂÜÖÔºâ
          const userInfo = (res.result && res.result.data && res.result.data.userInfo) || {};
          uni.setStorageSync('userInfo', userInfo);
          // ‰ºòÂÖà‰ΩøÁî®ÂêéÁ´ØËøîÂõûÁöÑÊ†áÂáÜ username Â≠óÊÆµ
          const username = userInfo && userInfo.username ? String(userInfo.username) : '';
          if (username) {
            // Áªü‰∏Ä‰ΩøÁî® 'username' ‰Ωú‰∏∫Êú¨Âú∞Â≠òÂÇ®ÈîÆ
            uni.setStorageSync('username', username);
            // ÂÖºÂÆπÊóßÈÄªËæëÔºåÂêåÊ≠•‰∏Ä‰ªΩÂà∞ currentUsernameÔºàÂèØÈÄêÊ≠•ÁßªÈô§Ôºâ
            uni.setStorageSync('currentUsername', username);
          }
          console.log('ÁôªÂΩïÂÜôÂÖ•Êú¨Âú∞Â≠òÂÇ®', { hasUserInfo: !!userInfo, username: uni.getStorageSync('username') });
          
          // Áªü‰∏ÄË∑≥ËΩ¨Âà∞È¶ñÈ°µÔºàÈÅøÂÖç‰ªéÊ≥®ÂÜåÈ°µ navigateBack ÂõûÈÄÄÂà∞Ê≥®ÂÜåÔºâ
          setTimeout(() => {
            uni.switchTab({
              url: '/pages/index/index',
              fail: () => uni.reLaunch({ url: '/pages/index/index' })
            });
          }, 1200);
        } else {
          // ÁôªÂΩïÂ§±Ë¥•
          throw new Error(res.result.message || 'ÁôªÂΩïÂ§±Ë¥•');
        }
      } catch (err) {
        console.error('ÁôªÂΩïÂ§±Ë¥•:', err);
        uni.showToast({
          title: err.message || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
          icon: 'none'
        });
      } finally {
        this.loading = false;
      }
    },
    
    navigateToRegister() {
      uni.navigateTo({
        url: '/pages/register/register'
      });
    }
  }
}
</script>

